.wp-block-zolo-review-carousel {
    position: relative;
    box-sizing: border-box;
    .swiper {
        --review-carousel-mask-offset: 300px;
    }
    &.default {
        .swiper {
            padding: 50px 0 0;
        }
    }

    .swiper-pagination-bullet {
        margin: 0 !important;
    }

    .swiper-pagination {
        --zolo-pagination-offset: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 12px;
        z-index: 11;
        bottom: 0 !important;
        transform: translateY(var(--zolo-pagination-offset));
        .swiper-pagination-bullet {
            background-color: transparent;
            width: 8px;
            height: 8px;
            border: 1px solid var(--zoloblocks-brand-color);
            transition: all 0.3s ease;

            &.swiper-pagination-bullet-active {
                transform: scale(1.3);
                background-color: var(--zoloblocks-brand-color);
            }
        }
    }

    .swiper-nav-button {
        position: absolute;
        top: 55%;
        z-index: 1;
        transform: translateY(-55%);
        width: clamp(2.188rem, 6vw, 3.125rem);
        height: clamp(2.188rem, 6vw, 3.125rem);
        color: #000;
        fill: #000;
        background-color: #fff;
        border-radius: 50%;
        transition: all 0.3s ease;
        &:hover {
            fill: var(--zoloblocks-brand-button-color);
            color: var(--zoloblocks-brand-button-color);
            background-color: var(--zoloblocks-brand-color);
        }

        &:after {
            font-size: clamp(0.625rem, 1.5vw, 0.875rem);
        }
        &.swiper-zolo-next,
        &.swiper-button-next {
            right: -26px;
        }
        &.swiper-zolo-prev,
        &.swiper-button-prev {
            left: -26px;
        }
    }

    // custom navigation icon
    .swiper-zolo-prev {
        .zolo__display-icon {
            display: inline-block;
        }
    }

    .swiper-zolo-prev,
    .swiper-zolo-next {
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        svg {
            width: 16px;
            height: 16px;
            display: block;
        }
    }

    .swiper-button-next:after,
    .swiper-rtl .swiper-button-prev:after {
        content: 'next';
    }

    .swiper-button-prev:after,
    .swiper-rtl .swiper-button-next:after {
        content: 'prev';
    }
}
// pagination css  start
.zolo-pagination-nav {
    display: inline-flex;
    align-items: center;
    border-radius: 5px;
    overflow: hidden;
    .page-numbers {
        transition: 0.2s;
        font-size: 14px;
        padding: 8px 15px;
        text-transform: uppercase;
        text-decoration: none;
        background: #f0f0f4;
        cursor: pointer;
        + .page-numbers {
            border-left: 1px solid #d9d9d9;
        }
        &.current {
            background-color: #c5c5c5;
        }
    }
}
// pagination css  end
